// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as XKey from '../../src/hd/xkey.js'
import * as Path from '../../src/hd/path.js'
import * as Private from '../../src/hd/private.js'
// import * as Public from '../../src/hd/public.js'
import fixtures from './fixtures/hdKey.json'

const HDKeyFixtures = fixtures
const network = 'main'

describe('Testing HD Key', function () {
  HDKeyFixtures.fromSeed.forEach(test => {
    it(`Creating HD key from phrase ${test[0]}`, function () {
      const childKey = Private.fromSeed(test[1], network)
      const xkey = Private.toString(childKey)
      assert.deepEqual(xkey, test[2])
    })
  })
  HDKeyFixtures.fromPath.forEach(test => {
    it(`Deriving HD key from xkey ${test[0]} with path ${
      test[1]
    }`, async function () {
      const path = Path.fromString(test[1])
      const parentKey = XKey.fromString(test[0])
      console.log('29. parentKey', parentKey)
      // console.log('29. hdKey', hdKey)
      // path.shift()
      let testIndex = 2
      // console.log('Parent - hdKey', hdKey)
      while (path.length) {
        const hdKey = await XKey.toPath(parentKey, path)
        // console.log('Child - hdKey', hdKey)
        const xprivkey = XKey.toString(hdKey, network)
        // console.log('Child - xprivkey', xprivkey)
        const publicKey = await XKey.toPublic(hdKey, network)
        const xpubkey = XKey.toString(publicKey, network)
        // console.log('Child - xpubkey', xpubkey)
        assert.equal(xprivkey, test[testIndex][0], 'xpriv')
        assert.equal(xpubkey, test[testIndex][1], 'xpub')
        testIndex++
      }
    })
  })
})
